const getScrollDirection = (currentTableSrollTop, prevTableSrollTop) => {  
  return ((currentTableSrollTop - prevTableSrollTop) >= 0) ? 'down' : 'up'
}

const animatedScrollTo = (element, to, duration, cb) => {  
  const start = element.scrollTop
  const change = to - start  
  const increment = 5
  let   currentTime = 0
      
  const animateScroll = (cb) => {      
      currentTime += increment      
      element.scrollTop = Math.easeInOutQuad(currentTime, start, change, duration)
      if(currentTime < duration) {        
        setTimeout( animateScroll.bind(null, cb), increment)
      } else {        
        cb()
      }
  }
  animateScroll(cb)
}

Math.easeInOutQuad = (t, b, c, d) => {
  t /= d/2;
  if (t < 1) return c/2*t*t + b;
  t--;
  return -c/2 * (t*(t-2) - 1) + b;
}

export {
  getScrollDirection,  
  animatedScrollTo    
}
