import _ from 'lodash'

export default class GetModel {
  constructor (originalData, columnsOptions, options = null, excludedColumns) {
    this.originalDataRows = originalData
    this.columnsOptions = columnsOptions
    this.excludedColumns = excludedColumns
    this.options = options
    this.processedData = {
      columns: {},
      rows: []
    }
  }

  mapDataRows () {
    this.processedData.options = this.options || {}
    const columns = JSON.parse(JSON.stringify(this.columnsOptions))
    columns.forEach((columnObj) => {
      const isColumnRemoved = this.excludedColumns.includes(columnObj.columnId)
      if (!isColumnRemoved) {
        this.processedData.columns[columnObj.columnId] = columnObj
      }
    })

    const getPricingTableRow = (originalRowObj) => {
      let newDataRow = {}
      _.forEach(this.columnsOptions, (columnOption) => {
        const isColumnRemoved = this.excludedColumns.includes(columnOption.columnId)
        if (!isColumnRemoved) {
          if (!columnOption.fields) {
            newDataRow[columnOption.columnId] = originalRowObj[columnOption.columnId]
          } else {
            let combinedObject = {}
            _.forEach(columnOption.fields, (fieldName) => {
              combinedObject[fieldName] = originalRowObj[fieldName]
            })
            newDataRow[columnOption.columnId] = combinedObject
          }
        }
      })

      return newDataRow
    }

    this.processedData.rows = _.map(this.originalDataRows, getPricingTableRow)    

    return this.processedData
  }
}
