<template>
  <div class="context-menu">
    <ul>
      <li v-if="selectedItems.selectedRows.length === 1" @click="copyRowToClipboard()">
        <div>Copy Row</div>
      </li>
      <li v-if="selectedItems.selectedRows.length > 1" @click="copyRowsToClipboard()">
        <div>Copy Rows</div>
      </li>
      <li v-if="selectedItems.selectedCell" @click="copyCellToClipboard()">
        <div>Copy Cell</div>
      </li>
    </ul>
  </div>
</template>

<script>
import _ from 'lodash'

export default {
  name: 'ContextMenu',
  props: {
    selectedItems: Object
  },
  methods: {
    copyRowToClipboard () {
      const columns = _.map(this.selectedItems.columns, 'displayName').join('\t')
      const row = _.map(Object.values(this.selectedItems.selectedRow[0]),  (val) => (val instanceof Object)
            ? _.reduce(
              Object.values(val), (sum, v) => sum + (v ? '' + v + ' ' : ''),
              '').trim()
            : val
        ).join('\t')
      const value = columns + '\r\n' + row
      this.$clipboard(value)
      return value
    },
    copyRowsToClipboard () {
      const columns = _.map(this.selectedItems.columns, 'displayName').join('\t')
      // define min and max Undefined
      // emit event to get them uploaded
      // wait until they are uploaded
      console.log('this.selectedItems.selectedRows', this.selectedItems.selectedRows)
      const undefinedRows = _.filter(this.selectedItems.selectedRows, (rowObj) => rowObj.value === undefined )
      const minUndefinedRow = _.minBy(undefinedRows, 'key')
      const maxUndefinedRow = _.maxBy(undefinedRows, 'key')
      console.log('minUndefinedRow', minUndefinedRow, 'maxUndefinedRow', maxUndefinedRow)
      const rows = _.reduce(this.selectedItems.selectedRows,
        (str, row) => str + _.map(Object.values(row.value),
          (val) => (val instanceof Object)
            ? _.reduce(
              Object.values(val), (sum, v) => sum + (v ? '' + v + ' ' : ''),
              '').trim()
            : val
        ).join('\t') + '\r\n', '')

      const value = columns + '\r\n' + rows
      this.$clipboard(value)
      return value
    },
    copyCellToClipboard () {
      this.$clipboard(this.selectedItems.selectedRow[0][this.selectedItems.selectedCell])
      return this.selectedItems.selectedRow[0][this.selectedItems.selectedCell]
    }
  }
}
</script>

<style scoped lang="scss">
  .context-menu {
    width: 128.6px;
    height: 91.8px;
    margin-top: 20px;
    box-shadow: -2px 2px 6px 0 rgba(0, 0, 0, 0.5);
    border-radius: 4.5px;
    background-color: #fff;
    ul {
      list-style-type: none;
      margin-left: 15.3px;
      display: flex;
      flex-direction: column;
      align-items: normal;
      height: 100%;
      li {
        width: 100%;
        margin: 8.7px 0 0 -40px;
        text-align: left;
        font-family: Poppins;
        font-size: 13px;
        color: #535561;

        &.inactive {
          color: rgba(83, 85, 97, 0.5);
          &:hover {
            background-color: #ffffff;
            cursor: auto;
          }
        }
        div:hover {
          background-color: #cccccc;
          font-weight: 600;
          cursor: pointer;
        }
        div {
          width: 130px;
          margin-left: -15px;
          padding: 5px 15px;
        }
      }
    }

  }

</style>
