<template v-slot:default="slotProps">
  <div        
    ref="tooltip"
    class="tooltip-container"
    v-b-tooltip.bottom.hover
    :title="this.$slots.default[0].text"
  ><slot></slot></div>
</template>

<script>
export default {
  name: 'earnixtooltip'
}
</script>

<style lang="scss">
  .tooltip-container {
    line-height: 13px;
    margin-top: 0px;
  }
    /* bootstrap tooltips custom styles*/
  .tooltip-inner {     
      color: #000 !important;    
      background-color: #fff !important;    
      border: 0.5px solid #ccc;
  }

  .arrow::before {
      z-index: -1;
  }
  .tooltip .arrow::before {
      border-bottom-color: #ccc !important;
  }
  .tooltip .arrow::after {
      top: 1px;
      position: absolute;
      content: "";
      border-color: transparent;
      border-style: solid;
      border-width: 0 0.4rem 0.4rem;
      border-bottom-color: #fff;
  }

</style>
