<template>
  <tbody @mouseover="() => processScrollOnExpandableArea('enter')" @mouseenter="() => processScrollOnExpandableArea('enter')" @mouseleave="processScrollOnExpandableArea('leave')">
    <tr
      @click.exact="selectRow(rowUniqueId, 'click')"
      @click.ctrl.exact="selectRow(rowUniqueId, 'ctrl+click')"
      @click.shift.exact="selectRow(rowUniqueId, 'shift+click')"
      @contextmenu="showContextMenu($event, rowUniqueId, null)"
      @auxclick.prevent      
      :class="[
                isRowExpanded? 'expanded-row': 'not-expanded-row',
                isRowSelected ? 'selected-row': ''
              ]"
    >
      <td class="column-for-expandable-icon" v-if="hasExpandedRows">
        <span class="expanded-arrow" @click="toggleInnerRow()">></span>        
      </td>
      <td
        v-for="(dataColumnValue, dataColumnName) in dataRow"
        :key="dataColumnName"
        @contextmenu="showContextMenu($event, rowUniqueId, dataColumnName)"
      >
        <earnix-cell
          :columnId="dataColumnName"
          :cellValue="dataColumnValue"
          :options="options"
          :class="isCellSelected(rowUniqueId, dataColumnName)? 'selected-cell': ''"
        ></earnix-cell>
      </td>
    </tr>
    <tr class="collapsible-row" >
      <td :colspan="Object.keys(options.columns).length + 1" class="collapsible-cell" :ref="`exp-${rowUniqueId}`">
        <b-collapse :visible="isRowExpanded" :id="`collapse-1-inner-${rowUniqueId}`">
          <slot :row="dataRow"></slot>
        </b-collapse>        
      </td>
    </tr>
  </tbody>
</template>

<script>
import EarnixCell from './earnix-cell'

export default {
  name: 'earnix-row',
  props: {
    options: Object,
    dataRow: Object,
    expandedRows: Array,
    selectedCell: Object,
    rowUniqueId: Number,
    selected: Boolean    
  },
  methods: {
    toggleInnerRow () {
      if (this.expandedRows.includes(this.rowUniqueId)) {
        this.$emit('rowCollapsed', this.rowUniqueId)
        this.$nextTick(
          () => {
            this.$emit('expandableaction', 'leave')
          }
        )
      } else {
        this.$emit('rowExpanded', this.rowUniqueId)
      }      
    },

    selectRow (rowId, trigger) {
      if (this.isRowSelected) {
        this.$emit('rowUnselected', rowId, trigger, this.dataRow)
      } else {
        this.$emit('rowSelected', rowId, trigger, this.dataRow)
      }
    },

    showContextMenu ($event, rowId, columnName) {
      $event.preventDefault()
      $event.stopPropagation()
      this.$emit('showContextMenu', [ $event, rowId, columnName ])
      this.$root.$emit('bv::hide::tooltip')
    },

    closeContextMenu () {
      this.$emit('closeContextMenu_' + (this.rowUniqueId))
    },

    isCellSelected (rowId, columnName) {
      return ((this.selectedCell.rowId === rowId) && (this.selectedCell.columnName === columnName))
    },
    processScrollOnExpandableArea (action) {
      if (this.isRowExpanded) {
          this.$emit('expandableaction',action)        
      }
    }
  },

  computed: {
    isRowSelected () {
      return this.selected
    },
    isRowExpanded () {
      return this.expandedRows.includes(this.rowUniqueId)
    },
    hasExpandedRows () {
      return !!this.$scopedSlots.default
    }
  },
  components: { EarnixCell }
}
</script>

<style lang="scss" scoped src="../styles/earnix-grid.scss"></style>
