<script>
import earnixGridComponent from './earnix-grid'
import _ from 'lodash'
import Clipboard from 'v-clipboard'
import Vue from 'vue'

Vue.use(Clipboard)

export default {
  name: 'earnix-grid-client',
  extends: earnixGridComponent,
  data(){
    return {
      totalCount: 0
    }
  },
  methods: {   
    processChangedOptions (newOptions, oldOptions) {
       this.totalCount = newOptions.rows.length
       if (this.scroll) {
        this.innerOptions = JSON.parse(JSON.stringify(newOptions))
        this.innerOptions.columns = oldOptions.columns || this.innerOptions.columns
        this.innerOptions.rows = _.slice(this.innerOptions.rows, this.offset, this.limit)
      }      
    },
     getUniqueRowId (index) {
      return index + this.offset
    },
    updateDataOnScroll (offset) {
      const optionsObj = JSON.parse(JSON.stringify(this.options))        
      this.innerOptions.rows = _.slice(optionsObj.rows, offset, offset + this.limit)
    },
    copyRows () {
      this.selectedItems = {
        columns: this.options.columns,
        selectedRows: this.options.rows.filter((row, i) => this.selectedRows.includes(i)),
        selectedRow: this.options.rows.filter((row, i) => this.selectedCell.rowId === i),
        selectedCell: this.selectedCell.columnName
      }      
      const columns = _.map(this.selectedItems.columns, 'displayName').join('\t')
      const rows = _.reduce(this.selectedItems.selectedRows,
        (str, row) => str + _.map(Object.values(row.value),
          (val) => (val instanceof Object)
            ? _.reduce(
              Object.values(val), (sum, v) => sum + (v ? '' + v + ' ' : ''),
              '').trim()
            : val
        ).join('\t') + '\r\n', '')
      const value = columns + '\r\n' + rows
      this.$clipboard(value)
    }
  }
}

</script>


