import _ from 'lodash'

const getBackend = () => {
  const rawBackend = window.localStorage.getItem('earnix-backend') || '{}'
  const authToken = _.get(JSON.parse(rawBackend), 'authToken')
  return {
    serverHost: process.env.server_host,
    serviceKey: process.env.service_key,
    serverPort: process.env.server_port,
    authToken
  }
}

export { getBackend }
