<template>
  <div id="top-bar">
    <div v-if="$store.state.pricingRules.rules.rows">
      <buttonIconed  :text="'Refresh'" :type="'refresh'" :callback="refresh" class="buttons refresh"></buttonIconed>
      <buttonIconed  :text="'Create New'" :type="'create'" :callback="refresh" class="buttons create"></buttonIconed>
    </div>
    <earnix-spinner v-if="loading"/>
  </div>
</template>
<script>
import buttonIconed from 'button-iconed'
import EarnixSpinner from 'earnix-spinner'

export default {
  name: 'topBar',
  data () {
    return {
      loading: false
    }
  },
  components: {
    buttonIconed, EarnixSpinner
  },
  methods: {
    async refresh () {
      (this.$route.params.page === '1') ? await this.$store.dispatch('loadPricingRules') : this.$router.push('/rules/1')
    }
  }
}
</script>

<style scoped lang="scss">
  @import "@/styles/settings.scss";
 #top-bar {
    height: 80px;
    width: 100%;
    font-size: 14px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;

    & > div {
      width: auto;
      display: flex;
      justify-content: flex-end;
    }

    .buttons {
      align-self: center;
      align-items: center
    }
    .create{
      margin-right: 6.1px;
    }
    .refresh{
      margin-right: 46px;
    }
  }
</style>
