<template>
  <div class="schedule-container nowrap">
    <div class="label">Start:<br>End:</div>
    <div class="date-time">{{ formatDateTime(value.start) }}<br>{{ formatDateTime(value.end) }}</div>
  </div>
</template>

<script>
import moment from 'moment'

export default {
  name: 'Schedule',
  props: {
    value: { type: Object, required: true }
  },
  methods: {
    formatDateTime (dateTime) {
      return moment(dateTime).format('DD/MM/YYYY HH:mm')
    }
  }
}
</script>

<style lang="scss" scoped>
  .schedule-container {
    display: flex;
    .label {
      color: #646d98;
    }
    .date-time {
      padding-left: 0.75em;
      color: #a1a8c2;
      font-weight: 300;
    }
  }
</style>
