<template>
  <div id="side-bar">
    <ul id="menu-items">
       <router-link v-for="item in entities"
        :class="{ 'hasSelection': item.toLowerCase() === selectedEntity }"
        @click.native="onSelect"
        v-bind:key="item" tag="li" :to="{ path: `/${item.toLowerCase()}` }">
        <div class="oval">icon</div>
        <div class="entity-desc">{{ item }}</div>
      </router-link>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'sidebar',
  data () {
    return {
      selectedEntity: null,
      entities: [
        'Rules',
        'Scripts',
        'Snapshots',
        'Logs',
        'Servers']
    }
  },
  methods: {
    onSelect () {
      this.changeSelectedEntity(this.$route.params.entity)
    },
    changeSelectedEntity (entity) {
      this.selectedEntity = entity
    }
  },
  mounted () {
    this.onSelect()
  },
  computed: {
    lineOfBusiness () {
      return this.$route.params.lineOfBusiness
    }
  },
  watch: {
    '$route' (to) {
      this.changeSelectedEntity(to.params.entity)
    }
  }

}
</script>

<style scoped lang="scss">
@import "@/styles/settings.scss";

  #side-bar {
    width: $leftBarWidth;
    margin-left: -$leftBarWidth;
    position: relative;

    ul {
      position: absolute;
      right: 15.4px;
      list-style-type: none;
    }

    ul >li {
      width: 94.7px;
      height: 103.7px;
      background-color: #26ae7a;
      margin-bottom: 1.9px;
      font-family: Poppins;
      font-size: 14px;
      color: $white;
      padding-top: 1px;
      text-align: center;
      cursor: pointer;
    }
    .oval {
      width: 28.2px;
      height: 28.2px;
      background-color: #229666;
      font-family: Poppins;
      font-size: 10px;
      color: #37cc8e;
      border-radius: 50%;
      margin: 16px 34px 15px 32px;
      padding: 6px 3.5px;
    }

    ul >li:hover {
      background-color: #32a073;
    }

    .hasSelection,
    .hasSelection:hover {
      background-color: #229666;
    }

    .hasSelection .oval {
      background-color: #26ae7a;
      color: #229666;
    }
  }

</style>
