import { pageLimit } from '@/views/rules/constants'

export default {
  async loadPricingRules ({ commit, rootState }, payload, limit = pageLimit) {
    const payloadObject = payload || { offset: 0, limit }
    payloadObject.limit = payloadObject.limit || limit
    if (payloadObject.sort) {
      commit('SET_SORTING_RULES', payloadObject.sort)
    } else if (rootState.pricingRules.currentSortingParams) {
      payloadObject.sort = rootState.pricingRules.currentSortingParams
    }
    const rules = await rootState.app.swaggerClient.apis['Pricing Execution'].getPricingRules(payloadObject)
    const result = JSON.parse(rules.data)
    const solution = rootState.app.solution
    commit('SET_RULES', { data: result.subSet, solution })
  },

  async getTotalCount ({ commit, rootState }) {
    const result = await rootState.app.swaggerClient.apis['Pricing Execution'].getPricingRules({
      limit: 1, offset: 0
    }) // ** This API should be replaced by a dedicated counter API when available**
    const { totalCount } = JSON.parse(result.data)
    commit('setTotal', totalCount)    
  }
}
