<template>
  <div :style="getCellWidth()">
    <component
      v-if="componentName"
      :is="componentName"
      :value="cellValue"
      :columnProp="properties"
      :class="['earnix-grid-cell', `${columnId}-column`]"></component>
    <div v-if="!componentName">
      <earnixtooltip :class="['earnix-grid-cell', `${columnId}-column`]" v-if="hasTooltip">{{ cellValue }}</earnixtooltip>
      <div v-else :class="`${columnId}-column` " >{{ cellValue }}</div>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
import earnixtooltip from 'earnix-tooltip'
import CreatedBy from './columns/created-by'
import DateTime from './columns/date-time'
import Status from './columns/status'
import StatusWithButtons from './columns/status-with-buttons'
import Schedule from './columns/schedule'
import TriggerType from './columns/trigger-type'

export default {
  name: 'earnix-cell',
  props: ['options', 'columnId', 'cellValue'],
  data () {
    return { properties: this.options.columns[this.columnId] }
  },
  methods: {
    getCellWidth () {
      return (this.options.columns[this.columnId].width) ? { width: this.options.columns[this.columnId].width } : ''
    }
  },
  computed: {
    componentName () {
      return _.get(this, 'properties.component')
    },
    hasTooltip () {
      return this.properties ? this.properties.tooltip : false
    }
  },
  components: { earnixtooltip, CreatedBy, DateTime, Status, StatusWithButtons, Schedule, TriggerType }
}
</script>

<style lang="scss" scoped src="../styles/earnix-grid.scss"></style>
