<template>
  <div id="status-with-buttons">
    <status :value="value.name" :column-prop="columnProp"/>
    <div class="row-buttons">
      <span><img src="../../assets/simulator-button.svg"></span>
      <span @click="onEditClick"><img src="../../assets/edit-button.svg"></span>
      <span><img src="../../assets/create-alike-button.svg"></span>
      <span><img src="../../assets/archive-button.svg"></span>
    </div>
  </div>
</template>

<script>
import Status from './status'

export default {
  name: 'StatusWithButtons',
  props: {
    value: { type: Object, required: true },
    columnProp: { type: Object, required: true }
  },
  components: { Status },
  methods: {
    onEditClick () {
      this.$root.$emit('grid-row-edit', this.value.id)
    }
  }
}
</script>

<style scoped lang="scss">
$statusWidth: 200px;

#status-with-buttons {
  width: $statusWidth;
  &:hover /deep/ .status {
    display: none;
  }
  &:hover .row-buttons {
    display: block;
  }
  .row-buttons {
    display: none;
    position: absolute;
    // if .status and .row-buttons will not match the image will blink on cursor on non-intersection
    top: 21px;
    width: $statusWidth;
    span {
      padding: 10px 10px;
      margin: 0 5px;
      border-radius: 50%;
      cursor: pointer;
      &:hover {
        background: #f0f3ff;
      }
    }
  }
}
</style>
