<template>
  <div id="app-container">
    <div class="app">
      <topNavbar></topNavbar>
      <div class="main-section row">
        <sidebar class="sidebar"></sidebar>
        <topBar></topBar>
        <div class="app-content-area">
          <transition v-if="mainComponentName" name="fade">
            <component class="app-content" v-bind:is="mainComponentName"></component>
          </transition>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
import Rules from '@/views/rules'

import Pricing from '@/components/pricing.vue'
import sidebar from '@/components/sidebar.vue'
import topNavbar from '@/components/top-navbar.vue'
import topBar from '@/components/topbar.vue'

export default {
  name: 'AppContainer',
  components: {
    topNavbar,
    topBar,
    sidebar,
    Rules,
    Pricing
  },
  computed: {
    mainComponentName () {
      const entityName = _.camelCase(this.$route.params.entity)
      if (!entityName || entityName === 'rules') {
        return 'Rules'
      } else {
        return 'Pricing'
      }
    }
  }
}
</script>

<style scoped lang="scss">
@import "@/styles/settings.scss";
#app-container {
  background-color: $paleGray;

  .app {
    width: 100%;
    .main-section {
      margin: 0px 13% 80px;
    }

    .sidebar{
      margin-top: 80px;
    }
  }
}

@media(max-width:1530px){
  #app-container .app .main-section {
      margin: 0px 5% 80px 10%;
    }
}

@media(max-width:1370px){
  #app-container .app .main-section {
      margin: 0px 5% 80px 10%;
    }
}

</style>
