<template>
  <div class="button-iconed" >
    <span class="text"  v-on:click="onClick">{{text}}</span>
    <img class="image" v-on:click="onClick" :src="selectedImage">
  </div>
</template>

<script>
export default {
  name: 'button-icon',
  props: {
    text: { type: String, required: true },
    type: { type: String, required: true },
    callback: { type: Function, required: true }
  },
  data () {
    return {
      imageList: {
        refresh: require('../assets/refresh-button.png'),
        create: require('../assets/create-new-button.png')
      }
    }
  },
  computed: {
    selectedImage: function () {
      return this.imageList[this.type]
    }
  },
  methods: {
    onClick () {
      this.callback()
    }
  }
}
</script>

<style scoped lang="scss">
  .button-iconed {
    height: min-content;
    vertical-align: middle;
    display:inline-block;
    box-sizing: content-box;
    user-select: none;

    .text{
      font-family: Poppins;
      font-size: 13px;
      font-weight: 500;
      color: #646d98;
      cursor: pointer;
    }

    .image{
      cursor: pointer;
      padding-left: 6.2px;
    }
  }
  .button-icon:hover{
    filter: drop-shadow( 0px 0px 5px #b6b6b6);
  }

  .button-icon:active{
    filter: drop-shadow( 0px 0px 5px #676767);
  }

</style>
