<template>
  <div class="spinner-loading-stub">
    <div class="ring-container" :style="{background: background || '#fff'}">
      <div class="ring">
        Loading
        <span></span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'earnix-spinner',
  props: ['background']
}
</script>
<style lang="css" scoped>
.spinner-loading-stub {
  z-index: 9999999;
}

.ring-container {
  position: absolute;
  width: 250px;
  height: 250px;
  top: 50%;
  left: 50%;
  margin-top: -125px;
  margin-left: -125px;
}

.ring {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100px;
  height: 100px;
  background: transparent;
  border: 1px solid #bababa;
  border-radius: 50%;
  text-align: center;
  line-height: 100px;
  font-size: 14px;
  color: #2c3e50;
  letter-spacing: 2px;
  text-transform: uppercase;
}
.ring:before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  border: 1px solid transparent;
  border-top: 0.5px solid #333;
  border-right: 0.5px solid #333;
  border-radius: 50%;
  animation: animateCircle 2s linear infinite;
}

.ring span {
  display: block;
  position: absolute;
  top: calc(50% - 1px);
  left: 50%;
  width: 50%;
  height: 1px;
  background: transparent;
  transform-origin: left;
  animation: animate 2s linear infinite;
}

.ring span:before {
  content: "";
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #333;
  top: -3px;
  right: -3.5px;
}

@keyframes animateCircle {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes animate {
  0% {
    transform: rotate(45deg);
  }
  100% {
    transform: rotate(405deg);
  }
}
</style>
