<template>
    <div class="nowrap">
        <earnixtooltip v-if="this.columnProp.tooltip">
            {{ formatCreatedTime(this.value).date }}
            <span
            class="time"
            v-if="formatCreatedTime(this.value).time"
            >{{ formatCreatedTime(this.value).time }}</span>
        </earnixtooltip>

        <div v-else>
            {{ formatCreatedTime(this.value).date }}
            <span
            class="time"
            v-if="formatCreatedTime(this.value).time"
            >{{ formatCreatedTime(this.value).time }}</span>
        </div>
    </div>
</template>

<script>
import moment from 'moment'
import earnixtooltip from 'earnix-tooltip'

export default {
  name: 'DateTime',
  props: {
    value: { type: String, required: true },
    columnProp: { type: Object, required: true }
  },
  methods: {
    formatCreatedTime (createdTime) {
      return {
        date: moment(createdTime).format('ll'),
        time: createdTime.includes(' ') ? moment(createdTime).format('h:m A') : ''
      }
    }
  },
  components: { earnixtooltip }
}
</script>

<style scoped lang="scss">
@import "../../styles/earnix-grid.scss";
    .time {
        font-weight: 200;
    }

    .time:before {
        content: "|";
        font-weight: 200;
        padding: 0 5px;
    }
</style>
