import GetModel from '../models/get-model'
import RulesColumnsAttributes from '../models/rules-columns-attributes'

export const mutations = {

  SET_RULES (state, payload) {
    const rules = payload.data
    const solution = payload.solution
    const GetRulesOptions = new GetModel(rules, RulesColumnsAttributes, { rowHeight: 68 }, (solution && solution === 'Insurance') ? [] : ['encounter'])
    state.rules = GetRulesOptions.mapDataRows()
  },
  setTotal (state, total) {
    state.total = total
  },
  setSwaggerClient (state, swaggerClient) {
    state.swaggerClient = swaggerClient
  },

  SET_SORTING_RULES (state, currentSortingParams) {
    state.currentSortingParams = currentSortingParams
  }
}
