<template>
  <div>
    <earnixtooltip v-if="this.columnProp.tooltip" :class="['status', checkStatus(this.value)]">
        {{ this.value }}
    </earnixtooltip>
    <div v-else :class="['status', checkStatus(this.value)]">{{ this.value }}</div>
  </div>
</template>

<script>
import earnixtooltip from 'earnix-tooltip'

const statuses = ['live', 'unreleased', 'draft', 'paused', 'completed', 'pending']

export default {
  name: 'Status',
  props: {
    value: { type: String, required: true },
    columnProp: { type: Object, required: true }
  },
  methods: {
    checkStatus (status) {
      return statuses.includes(status.toLowerCase()) ? status.toLowerCase() : ''
    }
  },
  components: { earnixtooltip }
}
</script>

<style scoped lang="scss">
@import "../../styles/earnix-grid.scss";

.status:before {
  content: "\25cf";
  padding-right: 9.5px;
}
.live {
  color: #7480f2;
}
.unreleased {
  color: #fcc349;
}
.draft {
  color: #afbed3;
}
.paused {
  color: #fa3d7b;
}
.completed {
  color: #2cc8b7;
}
.pending {
  color: #fcc349;
}
</style>
