'use strict'

import _ from 'lodash'
const Swagger = require('swagger-client')

const getInterceptor = (serviceKey, authToken) => (
  request =>
    _.set(request, 'headers', _.assign({
      'Content-Type': 'application/x-www-form-urlencoded',
      'service_key': serviceKey,
      'Accept': 'application/json'
    }, authToken ? { auth_token: authToken } : null)))

const createSwaggerClient = async (meta) => await Swagger({
  url: `http://${meta.serverHost}:${meta.serverPort}/${meta.urlPrefix || 'earnix'}/swagger.json`,
  requestInterceptor: getInterceptor(meta.serviceKey, meta.authToken),
  serverVariables: { BACKEND_HOST: meta.serverHost, BACKEND_PORT: meta.serverPort }
})

export { createSwaggerClient }
