<template>
    <div id="app">
      <transition name="fade">
        <div class="page-container">
          <app-container v-if="initialized" />
        </div>
      </transition>
    </div>
</template>
<script>
import { mapState } from 'vuex'
import store from './store/'
import appContainer from '@/components/app-container'

export default {
  data () {
    return { initialized: false }
  },
  async mounted () {
    await store.dispatch('initSwaggerClient')
    await store.dispatch('fetchSolutions')
    this.initialized = true
  },
  computed: mapState(['swaggerClient']),
  components: { appContainer }
}
</script>

<style lang="scss">
@import "@/styles/settings.scss";
html, body {
        margin: 0;
        height: 100%;
        background-color: $paleGray;
    }

    h1 {
        padding: 0;
        margin-top: 0;
    }
    #app {
        background: $paleGray;
        margin: 0;
        height: 100%;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        color: #2c3e50;
    }
    .page-container{
      position:absolute;
      width: 100%;
      height: 100vh;
    }

    @media(max-width: 1380px) {
      .page-container{
        width: 1380px;
        height: 100%;
      }
    }

</style>
