<template>
  <div>
    <div class="grid" v-if="innerOptions.rows" >
      <GlobalEvents
          @keyup.down.exact ="selectRowByArrow($event)"
          @keyup.up.exact ="selectRowByArrow($event)"
          @keyup.shift.down.exact="extendRowsSelectionByArrow($event)"
          @keyup.shift.up.exact="extendRowsSelectionByArrow($event)"
          @keyup.ctrl.67="copyRows"
      />
      <earnix-spinner v-if="loading"/>
      <div :class="getScrollClasses" :style="{ height: scrollContainerHeight + 'px', 'overflow-y': (isScrollable)?'auto':'hidden'}" @scroll="catchTableScroll($event)" @touchmove="catchTableScroll($event)" @wheel="catchTableScroll($event)" ref="tablescroll">
        <table class="rules-table table">
          <thead>
            <tr ref="rowref">
              <th class="column-for-expandable-icon" v-if="hasExpandedRows"></th>
              <th v-for="column in innerOptions.columns" :key="column.columnId">
                <div>{{ column.displayName }}
                  <div class="grid-sorting-container" v-if="column.sortable">
                    <div @click="processSorting(column)"
                        :class="getSortingClasses(column)" >
                    </div>
                  </div>
                </div>
              </th>
            </tr>
          </thead>           
          <tbody v-if="!(innerOptions.rows && innerOptions.rows.length)">
            <tr class="no-data-row">
              <td :colspan="Object.keys(innerOptions.columns).length + 1">
                There is no data to show
              </td>
            </tr>
          </tbody>
           <earnix-row v-for="(dataRow, index) in innerOptions.rows"
            :dataRow="dataRow"
            :options="innerOptions"
            :key="`${index}${offset}`"
            :expandedRows = "expandedRows"
            :rowUniqueId = "getUniqueRowId(index)"
            :selected="isRowSelected(index)"
            :selectedCell="selectedCell"
            :index="index"
            @showContextMenu="showContextMenu"
            @rowSelected = "selectRowByMethod"
            @rowUnselected = "unselectRowByMethod"
            @rowExpanded = "expandRow"
            @rowCollapsed = "collapseRow"
            @expandableaction="treatExpandableAction"
            v-on-clickaway="closeContextMenu"
            :ref="`row-${getUniqueRowId(index)}`"
            >
             <slot :row="dataRow"></slot>
           </earnix-row>           
        </table>
      </div>
      <div v-if="isScrollable" class="infinite-scroll-container" @scroll="processScrollBar($event)" :style="getScrollContainerStyles" ref="infscroll">
        <div class="infinite-scroll" :style="getCalculatedScrollHeight"></div>        
      </div>
      <ContextMenu v-if="isContextMenuShown" :style="getContextMenuPosition()" :selectedItems="selectedItems" />
    </div>
    <earnix-spinner background="transparent" v-else />
    <earnix-spinner background="transparent" v-if="showSpinner" />
  </div>
</template>
<script>
import GlobalEvents from 'vue-global-events'
import ContextMenu from './context-menu'
import EarnixSpinner from 'earnix-spinner'
import EarnixRow from './earnix-row'
import { mixin as clickaway } from 'vue-clickaway'
import { getScrollDirection, animatedScrollTo } from './helpers/scrolling'
import _ from 'lodash'

let fftimeout

export default {
  name: 'EarnixGrid',
  data () {
    return {
      offset: 0,      
      rowHeight: 0,
      prevOffset: 0,
      loading: false,
      rowsOnScreen: 10,
      expandedRows: [],
      selectedRows: [],
      focusedRow: null,
      selectedCell: {},
      scrollPosition: 0,      
      selectedItems: {},
      sortingTimer: null,      
      showSpinner: false,      
      prevTableSrollTop: 0,      
      selectedRowsData: {},
      openedContextMenu: {},      
      scrollDirection: null,
      prevCustomScrollTop: 0,
      scrollContainerHeight: 0,
      isScrollExpandedArea: false,
      initSortingTrigger: true,
      isContextMenuShown: false,      
      innerOptions: this.options,
      scrollAnimationInProcess: false      
    }
  },
  mounted () {    
    this.rowsOnScreen = this.displayRowsOnScreen || this.rowsOnScreen        
    this.rowHeight = (this.$refs.rowref)? this.$refs.rowref.clientHeight : this.rowHeight
    this.scrollContainerHeight = this.rowHeight * (this.rowsOnScreen + 1)
    this.$nextTick(() => {
      this.scrollContainerHeight = this.scrollContainerHeight || this.rowHeight * (this.rowsOnScreen + 1)
    })
    if (!this.isScrollable && this.options.rows) {
      this.rowsOnScreen = this.options.rows.length - 1
    } 
  },
  watch: {
    options (newOptions, oldOptions) {      
      this.rowHeight = (this.$refs.rowref) ? this.$refs.rowref.clientHeight : 68
      this.scrollContainerHeight = this.rowHeight * (this.rowsOnScreen + 1)      
      if (this.initSortingTrigger) {
        this.$emit('grid-event', 'sorted', this.getSortedColumns())
        this.initSortingTrigger = false
      }     
      clearTimeout(this.sortingTimer)
      this.loading = false
      this.processChangedOptions(newOptions, oldOptions)
    }
  },
  mixins: [ clickaway ],
  props: {
    options: Object,
    scroll: { type: Boolean, default: true },
    limit:  { type: Number, default: 30 },
    displayRowsOnScreen: Number
  },
  methods: {   
    getSortingClasses (column) {
      return {
        'grid-sorting-icon': true,
        'sorted': column.sortColumnPriority && column.sortDirection,
        'primary': column.sortColumnPriority === 1,
        'secondary': column.sortColumnPriority === 2,
        'asc': column.sortDirection && column.sortDirection === 'asc',
        'desc': column.sortDirection && column.sortDirection === 'desc'
      }
    },

    processSorting (column) {
      this.innerOptions.columns[column.columnId].sortDirection =
            (this.innerOptions.columns[column.columnId].sortDirection && this.innerOptions.columns[column.columnId].sortDirection === 'asc') ? 'desc' : 'asc'
      this.changeSortedFields(column)
      this.sortingTimer = setTimeout(() => {
        this.loading = true
      }, 500)
      this.$emit('grid-event', 'sorted', this.getSortedColumns())
    },

    changeSortedFields (column) {
      const isColumnNotSorted = !this.innerOptions.columns[column.columnId].sortColumnPriority
      if (isColumnNotSorted) {
        _.forEach(this.innerOptions.columns, (value, key) => {
          if (value.sortColumnPriority === 2) {
            delete this.innerOptions.columns[key].sortColumnPriority
          }
        })
        _.forEach(this.innerOptions.columns, (value, key) => {
          if (value.sortColumnPriority === 1) {
            this.innerOptions.columns[key].sortColumnPriority = 2
          }
        })

        this.innerOptions.columns[column.columnId].sortColumnPriority = 1
      }
      this.options.columns = this.innerOptions.columns
    },

    getSortedColumns () {
      let sortedColumns = []
      _.map([2, 1], (n) => {
        _.forEach(this.innerOptions.columns, (column, key) => {
          if (column.sortColumnPriority === n) {
            sortedColumns.push({ id: key, direction: column.sortDirection, order: n })
          }
        })
      })
      return _.sortBy(sortedColumns, ['order'])
    },

    expandRow (rowId) {
      this.expandedRows.push(rowId)
    },

    collapseRow (rowId) {
      this.expandedRows.splice(this.expandedRows.findIndex(r => r === rowId), 1)
    },

    // SCROLL METHODS //
    treatExpandableAction (action) {
      if (action === 'enter') {
        this.isScrollExpandedArea = true
      }
      if (action === 'leave') {
        this.isScrollExpandedArea = false
      }      
    },

    catchTableScroll (event) {      
      if (!this.isScrollable) return
      event.preventDefault()
      const normalizedDeltaY = (navigator.userAgent.indexOf('Firefox') > -1)? event.deltaY/3 * 100 : event.deltaY
      if (event.type==="wheel") {        
        if (this.isScrollExpandedArea) {          
          if (!this.scrollAnimationInProcess) {            
              this.scrollAnimationInProcess = true
              animatedScrollTo(this.$refs.tablescroll, this.$refs.tablescroll.scrollTop + normalizedDeltaY, 300, () => { this.scrollAnimationInProcess = false})
          }
          if (this.$refs.tablescroll.scrollTop === 0) {
            this.isScrollExpandedArea = false
          }
        } else {         
          this.$refs.infscroll.scrollTop += normalizedDeltaY
        }
      }     
    },   

    scrollSideEffects () {
      this.$root.$emit('bv::hide::tooltip') // hide tooltips on scrolling
      this.closeContextMenu()
    },

     processScrollBar (event) {       
      if (!this.isScrollable)  return

        const scrollTop = this.$refs.infscroll.scrollTop        
        this.scrollDirection = getScrollDirection(event.target.scrollTop, this.prevCustomScrollTop)
        this.prevCustomScrollTop = event.target.scrollTop

        this.scrollPosition = ~~(scrollTop/this.rowHeight)        
        if (this.scrollPosition > (this.totalCount - 8)) {
          this.scrollPosition = this.totalCount - 7
        }
        let tableScrollTop = (this.scrollPosition - this.offset ) * this.rowHeight         
        
        if (this.scrollDirection === 'down') {
          if (tableScrollTop < this.$refs.tablescroll.scrollTop) {
            tableScrollTop = this.$refs.tablescroll.scrollTop + this.rowHeight
          }
        }        

        this.offset = ~~(this.scrollPosition/(this.limit - 10)) * (this.limit - 10)
        if (this.offset !== this.prevOffset) {
          if (this.scrollDirection === 'down') {
              this.updateDataOnScroll(this.scrollPosition)
              this.$refs.tablescroll.scrollTop = 0
              tableScrollTop = this.rowHeight/2
          } else {                        
            this.updateDataOnScroll(this.offset)
            this.$refs.tablescroll.scrollTop = this.limit  * this.rowHeight       
            tableScrollTop = this.limit * this.rowHeight - 10 * this.rowHeight            
          }
        }
        if (this.scrollPosition === 0) {
          tableScrollTop = 0
        }        
        this.prevOffset = this.offset
        
        if (tableScrollTop !== this.prevTableSrollTop) {          
          if (this.checkExpandedRowsVisibility()) {
            if (!this.scrollAnimationInProcess) {            
              this.scrollAnimationInProcess = true
              animatedScrollTo(this.$refs.tablescroll, tableScrollTop, 400, () => { this.scrollAnimationInProcess = false})
            }
          } else {
            animatedScrollTo(this.$refs.tablescroll, tableScrollTop, 400, () => { this.scrollAnimationInProcess = false})
          }
          
        } else {          
          if (this.scrollDirection === 'down') {
            tableScrollTop += this.rowHeight
          } else {
            tableScrollTop -= this.rowHeight
          }
          if (!this.scrollAnimationInProcess) {            
            this.scrollAnimationInProcess = true
            animatedScrollTo(this.$refs.tablescroll, tableScrollTop, 400, () => { this.scrollAnimationInProcess = false})
          }
        }        
        this.prevTableSrollTop = tableScrollTop
        this.scrollSideEffects()
    },

    checkExpandedRowsVisibility () {
     return _.filter(this.expandedRows, (rowId) => 
                ((rowId >= this.scrollPosition - this.rowsOnScreen) && (rowId <= (this.scrollPosition + this.rowsOnScreen)) && (this.scrollPosition > this.rowsOnScreen))).length
    },    
   
    // END OF SCROLL METHODS //

    // SELECTION METHODS  //
    showContextMenu (eventData) {
      const [ $event, rowId, columnName ] = eventData
      $event.preventDefault()
      $event.stopPropagation()
      this.isContextMenuShown = false
      this.openedContextMenu.x = $event.x
      this.openedContextMenu.y = $event.y
      this.isContextMenuShown = true
      this.selectCell(rowId, columnName)
      const selectedRow = this.innerOptions.rows.filter((row, i) => this.selectedCell.rowId === this.getUniqueRowId(i))
      if (!this.selectedRows.includes(rowId)) {
        this.selectedRows = []
        this.selectRow(rowId, selectedRow)
      }

      this.selectedItems = {
        columns: this.innerOptions.columns,
        selectedRows: Object.keys(this.selectedRowsData).map(key => ({ key, value: this.selectedRowsData[key] })),
        selectedCell: this.selectedCell.columnName,
        selectedRow        
      }
      this.$root.$emit('bv::hide::tooltip')
    },

    getContextMenuPosition () {
      return { position: 'fixed', top: this.openedContextMenu.y + 'px', left: this.openedContextMenu.x + 'px' }
    },

    closeContextMenu () {
      this.isContextMenuShown = false
      this.selectedCell = {}
    },

    selectRowByMethod (rowId, trigger, dataRow) {
      if (trigger === 'click') {
        this.selectRow(rowId, dataRow)
      }
      if (trigger === 'ctrl+click') {
        this.selectRows(rowId, dataRow)
      }
      if (trigger === 'shift+click') {        
        this.selectRangeRows(rowId)
      }
    },

    unselectRowByMethod (rowId, trigger, dataRow) {
      if (trigger === 'click') {
        this.selectRow(rowId, dataRow)
      }
      if (trigger === 'ctrl+click') {
        this.unselectRow(rowId)
      }
    },

    selectRow (rowId, dataRow) {
      this.selectedRows = [rowId]
      this.focusedRow = rowId
      this.selectedRowsData = {}
      this.selectedRowsData[rowId] = dataRow
    },

    unselectRow (rowId) {
      this.selectedRows.splice(this.selectedRows.findIndex(r => r === rowId), 1)
      this.$delete(this.selectedRowsData, rowId)
    },

    selectRows (rowId, dataRow) {
      this.selectedRows.push(rowId)
      this.selectedRowsData[rowId] = dataRow
    },   

    selectRowByArrow (event) {
      event.stopPropagation()
      if (!this.selectedRows.length) {
        this.selectRow(0, this.innerOptions.rows[0])
      } else if (this.selectedRows.length === 1) {
        const currentIndex = this.selectedRows[0]
        if ((event.key === 'ArrowUp') && (this.selectedRows[0] !== 0)) {
          this.selectRow((currentIndex - 1), this.innerOptions.rows[currentIndex - 1])
        }
        if ((event.key === 'ArrowDown') && (this.selectedRows[0] !== this.innerOptions.rows.length - 1)) {
          this.selectRow((currentIndex + 1), this.innerOptions.rows[currentIndex + 1])
        }
      } else {
        if ((event.key === 'ArrowUp') && (_.min(this.selectedRows) !== 0)) {
          const minIndex = _.min(this.selectedRows)
          this.selectRow((minIndex - 1), this.innerOptions.rows[minIndex - 1])
        }
        if ((event.key === 'ArrowDown') && (_.max(this.selectedRows) !== this.innerOptions.rows.length - 1)) {
          const maxIndex = _.max(this.selectedRows)
          this.selectRow((maxIndex + 1), this.innerOptions.rows[maxIndex + 1])
        }
      }
      this.focusedRow = this.selectedRows[0]
      this.selectedRowsData = {}      
      this.selectedRowsData[this.focusedRow] = this.innerOptions.rows[this.focusedRow]      
      this.$refs.tablescroll.scrollTop = this.focusedRow * this.rowHeight
    },

    extendRowsSelectionByArrow (event) {      
      if ((event.key === 'ArrowUp') && (this.focusedRow !== 0)) {
        const currentFocusedRow = this.focusedRow        
        this.focusedRow = currentFocusedRow - 1
        this.selectRows(this.focusedRow, this.options.rows[currentFocusedRow - 1])
      }

      if ((event.key === 'ArrowDown') && (_.max(this.selectedRows) !== this.options.rows.length - 1)) {
        const maxId = _.max(this.selectedRows)
        this.selectRows((maxId + 1), this.options.rows[maxId + 1])
        this.focusedRow = maxId + 1
      }
    },

    selectRangeRows (rowId) {
      const minSelectedRow = _.min(this.selectedRows)
      const maxSelectedRow = _.max(this.selectedRows)      
      
      if (rowId < minSelectedRow) {
        for (let i = rowId; i < minSelectedRow; i++) {
          this.selectRows(i, this.options.rows[i])
        }
      }
      if (rowId > maxSelectedRow) {
        for (let i = rowId; i > maxSelectedRow; i--) {
          this.selectRows(i, this.options.rows[i])
        }
      }
      this.focusedRow = rowId
    },
    
    isRowSelected (index) {
      const rowId = this.getUniqueRowId(index)
      return this.selectedRows.includes(rowId) && this.isSelectable
    },

    selectCell (rowId, columnName) {
      this.selectedCell.rowId = rowId
      this.selectedCell.columnName = columnName
    },

    isCellSelected (rowId, columnName) {
      return (this.selectedCell.rowId === rowId && this.selectedCell.columnName === columnName)
    }

    // END OF SELECTION METHODS  //
  },
  computed: {
    hasExpandedRows () {
      return !!this.$scopedSlots.default
    },

    getCalculatedScrollHeight () {
      const height = this.totalCount * this.rowHeight + 2
      return { height: height + 'px' }
    },
    isCopyable () {
      return !(this.innerOptions.options && this.innerOptions.options.copyable === false)
    },

    isSelectable () {
      return !(this.innerOptions.options && this.innerOptions.options.selectable === false)
    },

    isScrollable () {      
      return ( (this.scroll === true) && !!this.options.rows && (this.options.rows.length >= this.limit) ||
      ((this.totalCount - this.offset) < this.limit) )
    },

    getScrollClasses () {
      return ['table-container', !this.isScrollable ? 'native-scroll' : '']
    },

    getRowHeight () {
      return this.rowHeight
    },

    getScrollContainerStyles () {
      return { height: this.scrollContainerHeight - this.rowHeight + 'px', top: this.rowHeight + 'px' }
    }
  },
  components: { ContextMenu, EarnixRow, GlobalEvents, EarnixSpinner }
}
</script>
<style lang="scss" scoped src="../styles/earnix-grid.scss"></style>
