
import { createSwaggerClient } from 'earnix-swagger-client'
import { getBackend } from './helpers'

const appModule = {
  state: {
    swaggerClient: null,
    solution: null
  },
  mutations: {
    setSolution (state, solution) {
      state.solution = solution
    },
    setSwaggerClient (state, swaggerClient) {
      state.swaggerClient = swaggerClient
    }
  },
  actions: {
    async fetchSolutions ({ commit, state }) {
      const solution = await state.swaggerClient.apis.Solution.getEarnixSolution()
      commit('setSolution', JSON.parse(solution.data).solution)
    },
    async initSwaggerClient ({ commit }) {
      const backend = getBackend()
      commit('setSwaggerClient', await createSwaggerClient(backend))
    }
  }
}

export default appModule
