<template>
  <div class="my-grid">
    <!-- <earnix-grid-server :options="rules" @grid-event="handleGridEvent" :totalCount="$store.state.pricingRules.total">
      <template v-slot="slotProps">
        <ExpandedComponent :row="slotProps.row" />
      </template>
    </earnix-grid-server> -->
      
    <earnix-grid-client :options="rules" @grid-event="handleGridEvent">
      <template v-slot="slotProps">
        <ExpandedComponent :row="slotProps.row" />
      </template>
    </earnix-grid-client>          
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { EarnixGridServer, EarnixGridClient } from 'earnix-grid'
import ExpandedComponent from '@/components/expanded-component'

// TODO: proably should be always scroll=true
export default {
  name: 'Rules',
  data(){
    return {
      limit: 357
    }
  }, 
  mounted () {    
    this.initRules({limit: this.limit, offset: 0})    
  },
  methods: {    
    initRules (payload) {
      this.$store.dispatch('getTotalCount')
      this.$store.dispatch('loadPricingRules', payload)
    },
    handleGridEvent (eventType, eventData) {
      switch(eventType){
        case 'sorted': this.sortGridData(eventData)
        break
        case 'offset-updated': this.handleOffset(eventData)
        break
        default:
        break
      }
    },
    sortGridData (sortArray) {
      const sortParams = sortArray.reduce((acc, currVal) => (acc + ((currVal.direction === 'asc') ? '+' : '-') + currVal.id + ','), '').slice(0, -1)
      this.initRules({limit: this.limit, offset: 0, sort: sortParams})
    },
    handleOffset (offset) {
      this.$store.dispatch('loadPricingRules', { limit: this.limit, offset })
    }
  },
  props: {},
  computed: {
    ...mapGetters(['rules'])
  },
  components: { ExpandedComponent, EarnixGridServer, EarnixGridClient }
}
</script>
<style lang="scss" >
@import "@/styles/settings.scss";
    .my-grid {
      width: 1600px;
    }
    .inner-row-container {
      width: 100%;
      height: auto;
      .rule-definition {
        font-weight: 600;
        color: $fontBlackColor;
      }
      .inner-row-item {
        margin: 30px 30px;
        min-height: 150px;
        -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
        box-shadow: 0 3px 18px rgba(204, 204, 204, 0.5);
        border-radius: 4.5px;
        padding: 20px;

        .inner-row-item-title {
          font-size: 20px;
        }
        .inner-row-item-info {
          padding: 20px;
          font-size: 16px;
        }
      }
      .status:before {
        content: "\25cf";
        padding-right: 9.5px;
      }

      .live {
        color: #7480f2;
      }

      .unreleased {
        color: #fcc349;
      }
    }

/* Columns customization */
    .earnix-grid-cell {
        &.product-column {
        direction: rtl;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        max-width: 180px;
        text-align: left;
        font-weight: 700;
        color: #646d98;
      }

      &.comment-column {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        max-width: 180px;
        text-align: left;
      }

      &.createdBy-column > div {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        max-width: 180px;
        text-align: left;
      }
    }

/* End of Columns customization */

</style>
